@import 'https://fonts.googleapis.com/css2?family=Noto+Sans:wght@300;400;500;600;700;900&display=swap';
@import 'https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400;700;900&display=swap';
@import 'https://fonts.googleapis.com/css2?family=Oswald:wght@200;400;700&family=Roboto+Condensed:wght@300;400;700;900&display=swap';
