  src: url("../fonts//Inter/Inter-Semibold.eot?#iefix") format("embedded-opentype"), url("../fonts//Inter/Inter-Semibold.woff2") format("woff2"), url("../fonts//Inter/Inter-Semibold.woff") format("woff"), url("../fonts//Inter/Inter-Semibold.ttf") format("truetype"), url("../fonts//Inter/Inter-Semibold.svg#fontawesome") format("svg");
}
